// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
	// base
	--font-family: "Google Sans", sans-serif;
	--font-family-two: "Google Sans Text", sans-serif;
	--content-width: 1170px;
	--container-offset: 0px;
	--container-width: calc(var(--content-width) + (var(--container-offset) * 2));

	//transitions
	--transition-br-1: border-radius .2s cubic-bezier(.2, 0, 0, 1);

	// colors
	--light-color: #fff;
	--bs-gutter-x: 0;
	--bs-font-sans-serif: "Google Sans", sans-serif;
	--bs-border-radius: 24px;
	--bs-secondary-rgb: rgb(248, 241, 246);
	--bs-border-radius: 48px;
	--btn-bg: #6442d6;
	--color-purple-1: #6442d6;
	--bs-btn-active-bg: #6442d6;
	--card-btn-hover-bg: #dcdaf5;
	--bs-body-bg: #fefbff;
	--color-pure-white: #fff;

	--text-color-black: #1a0056;
	--text-color-dark: #1c1b1d;
	--text-color-dark-2: #4d4256;
	--text-color-dark-3: #21182b;
	--text-color-dark-4: #1e0060;
	--text-color-dark-5: #3c4043;

	--text-color-grey-1: #787579;

	--text-color-light: #e6e1e3;
	--text-color-light-2: #cbc4cc;
	--text-color-light-3: #e2e0fb;
	--text-color-light-4: #e7deff;
	--text-color-light-5: #e2e0fb;
	--text-color-light-6: #f2f2f2;
	--text-color-light-7: #e8e0e8;

	--text-main: var(--text-color-dark);
	--bs-primary: red;
	--text-color-darker: #4d4256;

	--bg-play-btn-hover: #44474614;
	--bg-color-menu-btn-hover: red;
	--bg-color-menu-btn-active: #dcdaf5;
	--bg-color-menu-btn-active-2: linear-gradient(0deg, rgb(77 66 86 / 12%), rgb(77 66 86 / 12%));
	--bg-color-linear-3: linear-gradient(0deg, rgb(77 66 86 / 8%), rgb(77 66 86 / 8%));
	--bg-color-linear-4: linear-gradient(0deg, rgb(203 196 204 / 8%), rgb(203 196 204 / 8%));
	--bg-color-linear-5: linear-gradient(0deg, rgb(203 196 204 / 12%), rgb(203 196 204 / 12%));

	--bg-color-1: #f8f1f6;

	--menu-btn-outline: rgb(77, 66, 86);
	--menu-btn-font-size: 24px;
	--menu-bg-color-1: #F2ECEE;
	--bs-offcanvas-bg: #F2ECEE;


	--dark-mode-bg-color: #141314;
	--dark-mode-bg-color-2: #1c1b1d;
	--dark-mode-bg-color-3: #211f21;
	--dark-mode-bg-color-4: #e6e1e3;
	--dark-mode-bg-color-5: #553f5d;
	--dark-mode-bg-color-6: #45455a;
	--dark-mode-bg-color-7: #0f0e0f;
	--dark-mode-bg-btn-1: #9f86ff;
	--dark-mode-bg-btn-2: #A78FFF;

	--card-border-radius: 45px;
	--menu-item-border-radius: 48px;
	--border-radius-1: 48px;
	--border-radius-2: 4px;

	--footer-line: #E1E3E1;

	--footer-grid-template-columns: var(--footer-grid-template-columns-lg);
	--footer-grid-template-columns-lg: 3fr 1fr 1fr 1fr;
	--footer-grid-template-columns-sm: 1fr 1fr 1fr;
}


:root,
[data-bs-theme=light] {
	--bs-blue: #0d6efd;
	--bs-indigo: #6610f2;
	--bs-purple: #6442d6;
	--bs-pink: #d63384;
	--bs-red: #dc3545;
	--bs-orange: #fd7e14;
	--bs-yellow: #ffc107;
	--bs-green: #198754;
	--bs-teal: #20c997;
	--bs-cyan: #0dcaf0;
	--bs-black: #1c1b1d;
	--bs-white: #fff;
	--bs-gray: #6c757d;
	--bs-gray-dark: #343a40;
	--bs-gray-100: #f8f9fa;
	--bs-gray-200: #f8f1f6;
	--bs-gray-300: #dee2e6;
	--bs-gray-400: #ced4da;
	--bs-gray-500: #adb5bd;
	--bs-gray-600: #787579;
	--bs-gray-700: #495057;
	--bs-gray-800: #343a40;
	--bs-gray-900: #212529;
	--bs-primary: #0d6efd;
	--bs-secondary: #6c757d;
	--bs-success: #198754;
	--bs-info: #0dcaf0;
	--bs-warning: #ffc107;
	--bs-danger: #dc3545;
	--bs-light: #f8f9fa;
	--bs-dark: #212529;
	--bs-primary-rgb: 13, 110, 253;
	--bs-secondary-rgb: 108, 117, 125;
	--bs-success-rgb: 25, 135, 84;
	--bs-info-rgb: 13, 202, 240;
	--bs-warning-rgb: 255, 193, 7;
	--bs-danger-rgb: 220, 53, 69;
	--bs-light-rgb: 248, 249, 250;
	--bs-dark-rgb: 33, 37, 41;
	--bs-primary-text-emphasis: var(--text-color-dark-2);
	--bs-secondary-text-emphasis: #2b2f32;
	--bs-success-text-emphasis: #0a3622;
	--bs-info-text-emphasis: #055160;
	--bs-warning-text-emphasis: #664d03;
	--bs-danger-text-emphasis: #58151c;
	--bs-light-text-emphasis: #495057;
	--bs-dark-text-emphasis: #495057;
	--bs-primary-bg-subtle: #cfe2ff;
	--bs-secondary-bg-subtle: #e2e3e5;
	--bs-success-bg-subtle: #d1e7dd;
	--bs-info-bg-subtle: #cff4fc;
	--bs-warning-bg-subtle: #fff3cd;
	--bs-danger-bg-subtle: #f8d7da;
	--bs-light-bg-subtle: #fcfcfd;
	--bs-dark-bg-subtle: #ced4da;
	--bs-primary-border-subtle: #9ec5fe;
	--bs-secondary-border-subtle: #c4c8cb;
	--bs-success-border-subtle: #a3cfbb;
	--bs-info-border-subtle: #9eeaf9;
	--bs-warning-border-subtle: #ffe69c;
	--bs-danger-border-subtle: #f1aeb5;
	--bs-light-border-subtle: #e9ecef;
	--bs-dark-border-subtle: #adb5bd;
	--bs-white-rgb: 255, 255, 255;
	--bs-black-rgb: 0, 0, 0;
	--bs-font-sans-serif: "Google Sans", sans-serif, system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", "Noto Sans", "Liberation Sans", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
	--bs-font-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
	--bs-gradient: linear-gradient(180deg, rgba(255, 255, 255, 0.15), rgba(255, 255, 255, 0));
	--bs-body-font-family: var(--bs-font-sans-serif);
	--bs-body-font-size: 1rem;
	--bs-body-font-weight: 400;
	--bs-body-line-height: 1;
	--bs-body-color: #212529;
	--bs-body-color-rgb: 33, 37, 41;
	--bs-body-bg: #fefbff;
	--bs-body-bg-rgb: 255, 255, 255;
	--bs-emphasis-color: #000;
	--bs-emphasis-color-rgb: 0, 0, 0;
	--bs-secondary-color: rgba(33, 37, 41, 0.75);
	--bs-secondary-color-rgb: 33, 37, 41;
	--bs-secondary-bg: #e9ecef;
	--bs-secondary-bg-rgb: 233, 236, 239;
	--bs-tertiary-color: rgba(33, 37, 41, 0.5);
	--bs-tertiary-color-rgb: 33, 37, 41;
	--bs-tertiary-bg: #f8f9fa;
	--bs-tertiary-bg-rgb: 248, 249, 250;
	--bs-heading-color: inherit;
	--bs-link-color: #0d6efd;
	--bs-link-color-rgb: 13, 110, 253;
	--bs-link-decoration: underline;
	--bs-link-hover-color: #0a58ca;
	--bs-link-hover-color-rgb: 10, 88, 202;
	--bs-code-color: #d63384;
	--bs-highlight-color: #212529;
	--bs-highlight-bg: #fff3cd;
	--bs-border-width: 1px;
	--bs-border-style: solid;
	--bs-border-color: #dee2e6;
	--bs-border-color-translucent: rgba(0, 0, 0, 0.175);
	--bs-border-radius: 0.375rem;
	--bs-border-radius-sm: 0.25rem;
	--bs-border-radius-lg: 16px;
	--bs-border-radius-xl: 24px;
	--bs-border-radius-xxl: 48px;
	--bs-border-radius-2xl: var(--bs-border-radius-xxl);
	--bs-border-radius-pill: 50rem;
	--bs-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
	--bs-box-shadow-sm: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
	--bs-box-shadow-lg: 0 1rem 3rem rgba(0, 0, 0, 0.175);
	--bs-box-shadow-inset: inset 0 1px 2px rgba(0, 0, 0, 0.075);
	--bs-focus-ring-width: 0.25rem;
	--bs-focus-ring-opacity: 0.25;
	--bs-focus-ring-color: rgba(13, 110, 253, 0.25);
	--bs-form-valid-color: #198754;
	--bs-form-valid-border-color: #198754;
	--bs-form-invalid-color: #dc3545;
	--bs-form-invalid-border-color: #dc3545;
}

